import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import moment from 'moment';
import jQuery from 'jquery';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { getAdminDashboardSaga } from '../../../store/actions';

// eslint-disable-next-line no-unused-vars
const AdminApp = props => {
  // eslint-disable-next-line no-unused-vars
  const [mounted, setMounted] = useState(false);
  const [type, setType] = useState('today');
  const {
    adminDashboard: {
      productCount,
      driverCount,
      orderCountForToday: {
        orderCountDelivered,
        orderCountPending,
        orderCountRejected,
        orderCountTotal,
      },
    },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.dashboard);
  const dispatch = useDispatch();

  useEffect(() => {
    setMounted(true);
    dispatch(getAdminDashboardSaga({ type }));
    jQuery('[data-toggle="tooltip"]').tooltip();
    return () => setMounted(false);
  }, []);

  useEffect(() => {
    if (mounted) {
      dispatch(getAdminDashboardSaga({ type }));
    }
  }, [type]);

  const welcomeMessage = `Welcome Vehicle Owner !`;

  return (
    <div className="wrapper wrapper-content dashboard" id="dashboard">
      <h2>{welcomeMessage}</h2>
      <div className="row card_main">
        <div className="col-md-6 col-xs-12 col-lg-3">
          <div className="card three">
            <div className="card-content">
              <p className="title">Total Drivers</p>
              {/* <p className="count">{`$ ${orderTotal}`}</p> */}
              <p className="count">{driverCount}</p>
            </div>
          </div>
        </div>
        <div className="col-md-6 col-xs-12 col-lg-3">
          <div className="card four">
            <div className="card-content">
              <p className="title">Total Vehicles</p>
              {/* <p className="count">{`$ ${orderTotal}`}</p> */}
              <p className="count">{productCount}</p>
            </div>
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-sm-12 col-xs-12">
          <div className="top_action">
            <div className="col-xs-12 col pull-left" style={{ padding: 0 }}>
              <h2 className="dashbordstat">
                {`${type}'s Statistics`}
                <i
                  className="fa fa-info-circle"
                  style={{ marginLeft: 5, color: '#404E67' }}
                  aria-hidden="true"
                  data-toggle="tooltip"
                  data-placement="top"
                  title="You can check your today, week and month order detail by using this filter"
                />
              </h2>
              <div style={{ position: 'absolute', right: 0 }}>
                <select
                  className="form-control"
                  value={type}
                  style={{ width: 100, marginTop: -29 }}
                  onChange={e => setType(e.target.value)}
                >
                  <option value="today">Today</option>
                  <option value="week">Weekly</option>
                  <option value="month">Monthly</option>
                </select>
              </div>
              {type === 'today' && (
                <h5>{moment().format('dddd, MMMM DD, yyyy')}</h5>
              )}
              {type === 'week' && <h5>Current week</h5>}
              {type === 'month' && <h5>Current month</h5>}
            </div>
          </div>
          <div className="table-responsive">
            <table id="today" className="table table-striped table-bordered">
              <tbody>
                <tr>
                  <td>
                    {type === 'today' && "Today's "}
                    Total Vehicles
                  </td>
                  <td>{orderCountTotal}</td>
                </tr>
                <tr>
                  <td>
                    {type === 'today' && "Today's "}
                    Pending Vehicles
                  </td>
                  <td>{orderCountPending}</td>
                </tr>
                <tr>
                  <td>
                    {type === 'today' && "Today's "}
                    Approved Vehicles
                  </td>
                  <td>{orderCountDelivered}</td>
                </tr>
                <tr>
                  <td>
                    {type === 'today' && "Today's "}
                    Rejected Vehicles
                  </td>
                  <td>{orderCountRejected}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SetTokenHeader(AdminApp, axios);
